import { cast } from 'mobx-state-tree';

import { SlidesResourceContentChartFavorites } from '@yandex-music-int/resource-collection';

import { SlideContentChartFavoritesModel } from '../../../model/slide/content/variants/SlideContentChartFavorites.js';
import { slideContentItemResponseToModel } from '../shared/slideContentItemResponseToModel.js';

export const slideContentChartFavoritesResponseToModel = (
    response: SlidesResourceContentChartFavorites
): SlideContentChartFavoritesModel => {
    return {
        type: response.type,
        data: {
            value: response.data.value,
            unit: response.data.unit,
            unitOverride: response.data.unitOverride,
            valueSuffix: response.data.valueSuffix,
            description: response.data.description,
            isOrderVisible: response.data.isOrderVisible,
            items: cast(response.data.items.map(slideContentItemResponseToModel))
        }
    };
};
