import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentTypes } from '@yandex-music-int/resource-collection';

import { SlideContentItem } from '../shared/SlideContentItem.js';

export const SlideContentQuizData = types.model('SlideContentQuizData', {
    description: types.maybe(types.string),
    items: types.array(SlideContentItem)
});

export const SlideContentQuiz = types.model('SlideContentQuiz', {
    type: types.literal(SlidesResourceContentTypes.QUIZ),
    data: types.maybe(SlideContentQuizData)
});

export type SlideContentQuizModel = Instance<typeof SlideContentQuiz>;
export type SlideContentQuizDataModel = Instance<typeof SlideContentQuizData>;
