'use client';

import { CountUp } from 'countup.js';
import { ReactNode, useEffect, useMemo, useRef } from 'react';
import { useIntl } from 'react-intl';

import { SlidesResourceContentUnit } from '@yandex-music-int/resource-collection';
import { useToggle } from '@yandex-music-int/sol/common/hooks/useToggle.js';
import { SROnly } from '@yandex-music-int/sol/components/SROnly';
import { Heading } from '@yandex-music-int/sol/components/Typography';

import { SlideContentStatModel } from '../../../model/slide/content/shared/SlideContentStat.js';

import styles from './StatItem.module.scss';

interface Props {
    data: SlideContentStatModel;
    index?: number;
}

export const StatItem = ({ data, index }: Props): ReactNode => {
    const countupRef = useRef(null);
    const { formatMessage, formatNumber } = useIntl();
    const { state: initialValueVisibility, toggleFalse: setFalseInitialValueVisibility } = useToggle(true);

    const text = useMemo(() => {
        switch (data.unit) {
            case SlidesResourceContentUnit.TEXT:
                if (data.unitOverride) {
                    return data.unitOverride;
                }
                break;
            case SlidesResourceContentUnit.MINUTES:
                return formatMessage({ id: 'slides-stats.minutes' }, { value: data.value });
            case SlidesResourceContentUnit.HOURS:
                return formatMessage({ id: 'slides-stats.hours' }, { value: data.value });
            case SlidesResourceContentUnit.MINUTES_WITH_MUSIC:
                return formatMessage({ id: 'slides-stats.minutes-with-music' }, { value: data.value });
            case SlidesResourceContentUnit.LIKES:
                return formatMessage({ id: 'slides-stats.likes' }, { value: data.value });
            case SlidesResourceContentUnit.BOOKS:
                return formatMessage({ id: 'slides-stats.books' }, { value: data.value });
            case SlidesResourceContentUnit.MINUTES_WITH_BOOKS:
                return formatMessage({ id: 'slides-stats.minutes-with-books' }, { value: data.value });
            case SlidesResourceContentUnit.TRACKS:
                return formatMessage({ id: 'slides-stats.tracks' }, { value: data.value });
            case SlidesResourceContentUnit.LISTENERS:
                return formatMessage({ id: 'slides-stats.listeners' }, { value: data.value });
            case SlidesResourceContentUnit.LISTENINGS:
                return formatMessage({ id: 'slides-stats.listenings' }, { value: data.value });
            case SlidesResourceContentUnit.NEW_TRACKS:
                return formatMessage({ id: 'slides-stats.new-tracks' }, { value: data.value });
            case SlidesResourceContentUnit.SUPERFANS:
                return formatMessage({ id: 'slides-stats.superfans' }, { value: data.value });
        }
        return '';
    }, [data.unit, data.unitOverride, data.value, formatMessage]);

    useEffect(() => {
        if (countupRef.current) {
            const countUpAnim = new CountUp(countupRef.current, Number(data.value), {
                startVal: 1,
                formattingFn: formatNumber
            });
            if (!countUpAnim.error) {
                countUpAnim.start();
                setFalseInitialValueVisibility();
            }
        }
    }, [data.value, formatNumber, setFalseInitialValueVisibility]);

    const style = useMemo(() => {
        if (index) {
            return {
                opacity: 1 - index * 0.2
            };
        }
        return;
    }, [index]);

    return (
        <>
            <SROnly>
                {data.value} {data.valueSuffix} {text}
            </SROnly>
            <div className={styles.root} style={style} aria-hidden>
                <div className={styles.valueContainer}>
                    {initialValueVisibility && (
                        <Heading variant="h3" size="xxxl">
                            1
                        </Heading>
                    )}
                    <Heading variant="h3" size="xxxl" tabIndex={-1} ref={countupRef} aria-hidden />
                    {data.valueSuffix && (
                        <Heading variant="h3" size="xxxl">
                            {data.valueSuffix}
                        </Heading>
                    )}
                </div>
                <Heading variant="h3" size="xl" weight="black" className={styles.unit}>
                    {text}
                </Heading>
            </div>
        </>
    );
};
