'use client';

import { ReactNode, useMemo } from 'react';

import { SlideContentChartFavoritesDataModel } from '../../../model/slide/content/variants/SlideContentChartFavorites.js';
import { ChartBlock } from '../Chart/ChartBlock.js';
import { StatItem } from '../StatItem/StatItem.js';

import styles from './ChartFavoriteContent.module.scss';

interface Props {
    data: SlideContentChartFavoritesDataModel;
}

export const ChartFavoriteContent = ({ data }: Props): ReactNode => {
    const statItemData = useMemo(() => {
        return {
            value: data.value,
            valueSuffix: data.valueSuffix,
            unit: data.unit,
            unitOverride: data.unitOverride
        };
    }, [data.unit, data.unitOverride, data.value, data.valueSuffix]);

    return (
        <div className={styles.root}>
            <StatItem data={statItemData} />
            <ChartBlock items={data.items} isOrderVisible={data.isOrderVisible} />
        </div>
    );
};
