import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentTypes } from '@yandex-music-int/resource-collection';

export const SlideContentTextData = types.model('SlideContentTextData', {
    title: types.maybe(types.string),
    subtitle: types.maybe(types.string)
});

export const SlideContentText = types.model('SlideContentText', {
    type: types.literal(SlidesResourceContentTypes.TEXT),
    data: types.maybe(SlideContentTextData)
});

export type SlideContentTextModel = Instance<typeof SlideContentText>;
export type SlideContentTextDataModel = Instance<typeof SlideContentTextData>;
