import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceButtonTypes } from '@yandex-music-int/resource-collection';

const SlideButtonActionDataAction = types.model('SlideButtonActionData', {
    deeplink: types.maybe(types.maybeNull(types.string)),
    weblink: types.maybe(types.maybeNull(types.string))
});

const SlideButtonActionData = types.model('SlideButtonActionData', {
    title: types.string,
    imageUrl: types.maybe(types.string),
    action: SlideButtonActionDataAction
});

export const SlideButtonAction = types.model('SlideButtonAction', {
    type: types.literal(SlidesResourceButtonTypes.ACTION),
    data: SlideButtonActionData
});

export type SlideButtonActionModel = Instance<typeof SlideButtonAction>;
export type SlideButtonActionDataModel = Instance<typeof SlideButtonActionData>;
