import { SlidesResourceContentText } from '@yandex-music-int/resource-collection';

import { SlideContentTextModel } from '../../../model/slide/content/variants/SlideContentText.js';

export const slideContentTextResponseToModel = (response: SlidesResourceContentText): SlideContentTextModel => {
    return {
        type: response.type,
        data: {
            title: response.data.title,
            subtitle: response.data.subtitle
        }
    };
};
