import { cast } from 'mobx-state-tree';

import { SlidesResourceContentQuiz } from '@yandex-music-int/resource-collection';

import { SlideContentQuizModel } from '../../../model/slide/content/variants/SlideContentQuiz.js';
import { slideContentItemResponseToModel } from '../shared/slideContentItemResponseToModel.js';

export const slideContentQuizResponseToModel = (response: SlidesResourceContentQuiz): SlideContentQuizModel => {
    return {
        type: response.type,
        data: {
            description: response.data.description,
            items: cast(response.data.items.map(slideContentItemResponseToModel))
        }
    };
};
