import { SlidesResourceButtonAction, SlidesResourceButtonTypes } from '@yandex-music-int/resource-collection';

import { SlideButtonActionModel } from '../../model/slide/button/SlideButtonAction.js';

export const slideButtonActionResponseToModel = (response: SlidesResourceButtonAction): SlideButtonActionModel => {
    return {
        type: SlidesResourceButtonTypes.ACTION,
        data: {
            title: response.data.title,
            imageUrl: response.data.imageUrl,
            action: {
                deeplink: response.data.action.deeplink,
                weblink: response.data.action.weblink
            }
        }
    };
};
