import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceButtonTypes } from '@yandex-music-int/resource-collection';

const SlideButtonWaveDataColors = types.model('SlideButtonWaveDataColors', {
    average: types.string,
    waveText: types.string
});

const SlideButtonWaveData = types.model('SlideButtonWaveData', {
    title: types.string,
    station: types.string,
    seeds: types.array(types.string),
    imageUrl: types.maybe(types.string),
    animationUrl: types.maybe(types.string),
    colors: types.maybe(SlideButtonWaveDataColors)
});

export const SlideButtonWave = types.model('SlideButtonWave', {
    type: types.literal(SlidesResourceButtonTypes.WAVE),
    data: SlideButtonWaveData
});

export type SlideButtonWaveModel = Instance<typeof SlideButtonWave>;
