import {
    SlidesResourceSlide,
    SlidesResourceButtonTypes,
    SlidesResourceContentTypes
} from '@yandex-music-int/resource-collection';

import { SlideModel } from '../model/slide/Slide.js';

import { slideButtonActionResponseToModel } from './button/slideButtonActionResponseToModel.js';
import { slideContentChartFavoritesResponseToModel } from './content/variants/slideContentChartFavoritesResponseToModel.js';
import { slideContentChartResponseToModel } from './content/variants/slideContentChartResponseToModel.js';
import { slideContentQuizResponseToModel } from './content/variants/slideContentQuizResponseToModel.js';
import { slideContentSingleEntityResponseToModel } from './content/variants/slideContentSingleEntityResponseToModel.js';
import { slideContentStatsResponseToModel } from './content/variants/slideContentStatsResponseToModel.js';
import { slideContentTextResponseToModel } from './content/variants/slideContentTextResponseToModel.js';

export const slideResponseToModel = (response: SlidesResourceSlide): SlideModel => {
    let button;
    if (response.button?.type) {
        switch (response.button.type) {
            case SlidesResourceButtonTypes.ACTION:
                button = slideButtonActionResponseToModel(response.button);
                break;
        }
    }

    let content;
    if (response.content?.type) {
        switch (response.content?.type) {
            case SlidesResourceContentTypes.CHART:
                content = slideContentChartResponseToModel(response.content);
                break;

            case SlidesResourceContentTypes.CHART_FAVORITES:
                content = slideContentChartFavoritesResponseToModel(response.content);
                break;

            case SlidesResourceContentTypes.QUIZ:
                content = slideContentQuizResponseToModel(response.content);
                break;

            case SlidesResourceContentTypes.SINGLE_ENTITY:
                content = slideContentSingleEntityResponseToModel(response.content);
                break;

            case SlidesResourceContentTypes.STATS:
                content = slideContentStatsResponseToModel(response.content);
                break;

            case SlidesResourceContentTypes.TEXT:
                content = slideContentTextResponseToModel(response.content);
                break;
        }
    }

    return {
        id: response.id,

        background: {
            bgImageUrl: response.background.bgImageUrl ?? '',
            videoUrl: response.background.videoUrl ?? '',
            muted: !response.background.withSound
        },

        button,
        content
    };
};
