import { Caption } from '@yandex-music-int/sol/components/Typography';

import { SlideContentQuizDataModel } from '../../../model/slide/content/variants/SlideContentQuiz.js';
import { ChartBlock } from '../Chart/ChartBlock.js';
import { ChartItemVariant } from '../Chart/ChartItem.js';

import styles from './QuizContent.module.scss';

interface Props {
    data: SlideContentQuizDataModel;
}

export const QuizContent = ({ data }: Props): ReactNode => {
    return (
        <div className={styles.root}>
            {data.description && (
                <Caption variant="span" type="text" size="l" weight="medium" className={styles.text}>
                    {data.description}
                </Caption>
            )}
            <ChartBlock items={data.items} variant={ChartItemVariant.QUIZ} isOrderVisible={false} />
        </div>
    );
};
