import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentCoverTypes } from '@yandex-music-int/resource-collection';

import { SlideContentCover } from './SlideContentCover.js';

export const SlideContentItem = types.model('SlideContentItem', {
    title: types.string,
    subtitle: types.maybe(types.string),
    cover: types.maybe(SlideContentCover),
    coverType: types.enumeration(Object.values(SlidesResourceContentCoverTypes))
});

export type SlideContentItemModel = Instance<typeof SlideContentItem>;
