import { ReactNode } from 'react';

import { Caption } from '@yandex-music-int/sol/components/Typography';

import { SlideContentStatModel } from '../../../model/slide/content/shared/SlideContentStat.js';
import { SlideContentStatsDataModel } from '../../../model/slide/content/variants/SlideContentStats.js';
import { StatItem } from '../StatItem/StatItem.js';

import styles from './StatsContent.module.scss';

interface Props {
    data: SlideContentStatsDataModel;
}

export const StatsContent = ({ data }: Props): ReactNode => {
    return (
        <div className={styles.root}>
            <Caption variant="span" type="text" size="l" weight="medium" className={styles.text}>
                {data.header}
            </Caption>
            {data.stats.map((statItem: SlideContentStatModel, index: number) => (
                <StatItem data={statItem} index={index} key={statItem.value} />
            ))}
            <Caption variant="span" type="text" size="l" weight="medium" className={styles.text}>
                {data.footer}
            </Caption>
        </div>
    );
};
