'use client';

import { ReactNode } from 'react';

import { SlidesResourceContentTypes } from '@yandex-music-int/resource-collection';

import { SlideContentChartModel } from '../../model/slide/content/variants/SlideContentChart.js';
import { SlideContentChartFavoritesModel } from '../../model/slide/content/variants/SlideContentChartFavorites.js';
import { SlideContentQuizModel } from '../../model/slide/content/variants/SlideContentQuiz.js';
import { SlideContentSingleEntityModel } from '../../model/slide/content/variants/SlideContentSingleEntity.js';
import { SlideContentStatsModel } from '../../model/slide/content/variants/SlideContentStats.js';
import { SlideContentTextModel } from '../../model/slide/content/variants/SlideContentText.js';

import { ChartContent } from './ChartContent/ChartContent.js';
import { ChartFavoriteContent } from './ChartFavoriteContent/ChartFavoriteContent.js';
import { QuizContent } from './QuizContent/QuizContent.js';
import { SingleEntityContent } from './SingleEntityContent/SingleEntityContent.js';
import { StatsContent } from './StatsContent/StatsContent.js';
import { TextContent } from './TextContent/TextContent.js';

interface Props {
    content:
        | SlideContentChartModel
        | SlideContentChartFavoritesModel
        | SlideContentQuizModel
        | SlideContentSingleEntityModel
        | SlideContentTextModel
        | SlideContentStatsModel;
}

export const SlideContent = ({ content }: Props): ReactNode => {
    if (content.data) {
        switch (content.type) {
            case SlidesResourceContentTypes.TEXT:
                return <TextContent data={content.data} />;
            case SlidesResourceContentTypes.STATS:
                return <StatsContent data={content.data} />;
            case SlidesResourceContentTypes.CHART:
                return <ChartContent data={content.data} />;
            case SlidesResourceContentTypes.CHART_FAVORITES:
                return <ChartFavoriteContent data={content.data} />;
            case SlidesResourceContentTypes.SINGLE_ENTITY:
                return <SingleEntityContent data={content.data} />;
            case SlidesResourceContentTypes.QUIZ:
                return <QuizContent data={content.data} />;
        }
    }
    return null;
};
