import { cast } from 'mobx-state-tree';

import { SlidesResourceContentItem } from '@yandex-music-int/resource-collection';

import { SlideContentItemModel } from '../../../model/slide/content/shared/SlideContentItem.js';

export const slideContentItemResponseToModel = (response: SlidesResourceContentItem): SlideContentItemModel => {
    return cast({
        title: response.title,
        subtitle: response.subtitle,
        cover: response.cover,
        coverType: response.coverType
    });
};
