import { clsx } from 'clsx';
import { ReactNode } from 'react';

import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption } from '@yandex-music-int/sol/components/Typography';

import { WithClassName } from '@/shared/lib/index.js';
import { Link } from '@/shared/ui/index.js';

import { SlideButtonActionDataModel } from '../../../model/slide/button/SlideButtonAction.js';

import styles from './ActionButton.module.scss';

interface Props extends WithClassName {
    data: SlideButtonActionDataModel;
}

export const ActionButton = ({ data, className }: Props): ReactNode => {
    const url = data.action.weblink || '';
    return (
        <Link href={url} className={clsx(styles.root, className)}>
            <Paper radius="xs" className={styles.cover}>
                <Image src={data.imageUrl} withAvatarReplace fit="contain" className={styles.image} />
            </Paper>
            <Caption variant="span" type="controls" size="l" weight="medium" className={styles.text} lineClamp={1}>
                {data.title}
            </Caption>
            <Icon variant="arrowRight" size="xs" className={styles.icon} />
        </Link>
    );
};
