import { SlidesResourceContentSingleEntity } from '@yandex-music-int/resource-collection';

import { SlideContentSingleEntityModel } from '../../../model/slide/content/variants/SlideContentSingleEntity.js';
import { slideContentCoverResponseToModel } from '../shared/slideContentCoverResponseToModel.js';

export const slideContentSingleEntityResponseToModel = (
    response: SlidesResourceContentSingleEntity
): SlideContentSingleEntityModel => {
    return {
        type: response.type,
        data: {
            title: response.data.title,
            subtitle: response.data.subtitle,
            description: response.data.description,
            cover: slideContentCoverResponseToModel(response.data.cover),
            coverType: response.data.coverType,
            smallRoundCover: response.data.smallRoundCover
                ? slideContentCoverResponseToModel(response.data.smallRoundCover)
                : undefined
        }
    };
};
