import { clsx } from 'clsx';
import { ReactElement, useMemo } from 'react';

import { SlidesResourceContentCoverTypes } from '@yandex-music-int/resource-collection';

import { SlideContentItemModel } from '../../../model/slide/content/shared/SlideContentItem.js';

import styles from './ChartBlock.module.scss';
import { ChartItem, ChartItemVariant } from './ChartItem.js';

interface Props {
    isOrderVisible: boolean;
    items: SlideContentItemModel[];
    variant?: ChartItemVariant;
}

export const ChartBlock = ({ items, isOrderVisible, variant }: Props): ReactElement => {
    const computedVariant = useMemo(() => {
        if (variant === ChartItemVariant.QUIZ) {
            return variant;
        }
        const artistsItems = items.every((item: SlideContentItemModel) => {
            return item.coverType === SlidesResourceContentCoverTypes.CIRCLE;
        });
        if (artistsItems) {
            return ChartItemVariant.ARTISTS;
        }
        return ChartItemVariant.TRACKS;
    }, [items, variant]);

    return (
        <ol className={clsx(styles.root, styles[`root_variant_${computedVariant}`])} tabIndex={-1}>
            {items.map((item: SlideContentItemModel, index: number) => (
                <ChartItem
                    data={item}
                    index={index}
                    variant={computedVariant}
                    isOrderVisible={isOrderVisible}
                    key={item.title}
                />
            ))}
        </ol>
    );
};
