'use client';

import { ReactNode } from 'react';

import { Caption } from '@yandex-music-int/sol/components/Typography';

import { SlideContentChartDataModel } from '../../../model/slide/content/variants/SlideContentChart.js';
import { ChartBlock } from '../Chart/ChartBlock.js';

import styles from './ChartContent.module.scss';

interface Props {
    data: SlideContentChartDataModel;
}

export const ChartContent = ({ data }: Props): ReactNode => {
    return (
        <div className={styles.root}>
            {data.description && (
                <Caption variant="span" type="text" size="l" weight="medium" className={styles.text}>
                    {data.description}
                </Caption>
            )}
            <ChartBlock items={data.items} isOrderVisible={data.isOrderVisible} />
        </div>
    );
};
