import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentTypes } from '@yandex-music-int/resource-collection';

import { SlideContentItem } from '../shared/SlideContentItem.js';

export const SlideContentChartData = types.model('SlideContentChartData', {
    description: types.maybe(types.string),
    isOrderVisible: types.boolean,
    items: types.array(SlideContentItem)
});

export const SlideContentChart = types.model('SlideContentChart', {
    type: types.literal(SlidesResourceContentTypes.CHART),
    data: types.maybe(SlideContentChartData)
});

export type SlideContentChartModel = Instance<typeof SlideContentChart>;
export type SlideContentChartDataModel = Instance<typeof SlideContentChartData>;
