import { cast, flow, getEnv, Instance, types } from 'mobx-state-tree';

import { SlidesResourceArtistParams, SlidesResourceResponse } from '@yandex-music-int/resource-collection';

import { LoadingState, StoreEnv } from '@/shared/lib/index.js';

import { slideResponseToModel } from '../lib/slideResponseToModel.js';

import { Slide } from './slide/Slide.js';
import { SlidesConsumer } from './SlidesConsumer.js';

export const Slides = types
    .model('Slides', {
        userSlidesLoadingState: types.enumeration(Object.values(LoadingState)),
        artistSlidesLoadingState: types.enumeration(Object.values(LoadingState)),
        consumer: types.maybe(types.enumeration(Object.values(SlidesConsumer))),
        activeSlide: types.optional(types.number, 0),
        userItems: types.array(Slide),
        artistItems: types.array(Slide)
    })
    //  ,     
    // eslint-disable-next-line @typescript-eslint/typedef
    .actions((self) => {
        return {
            getUserSlides: flow(function* (): Generator<Promise<SlidesResourceResponse>, void, SlidesResourceResponse> {
                const { slidesResource, logger } = getEnv<StoreEnv>(self);

                if (self.userSlidesLoadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.userSlidesLoadingState = LoadingState.PENDING;

                    const data = yield slidesResource.getUserSlides();

                    // @ts-expect-error
                    // TS ,     ,         ,
                    //      / ,     IDLE
                    if (self.userSlidesLoadingState !== LoadingState.IDLE) {
                        self.userSlidesLoadingState = LoadingState.RESOLVE;
                    }

                    if (data.slides) {
                        self.consumer = SlidesConsumer.USER;
                        self.userItems = cast(data.slides.map(slideResponseToModel));
                    }

                    self.userSlidesLoadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (error instanceof Error) {
                        logger.error(error);
                    }

                    if (self.userSlidesLoadingState !== LoadingState.IDLE) {
                        self.userSlidesLoadingState = LoadingState.REJECT;
                        self.userItems = cast([]);
                    }
                }
            }),

            getArtistSlides: flow(function* (
                params: SlidesResourceArtistParams
            ): Generator<Promise<SlidesResourceResponse>, void, SlidesResourceResponse> {
                const { slidesResource, logger } = getEnv<StoreEnv>(self);

                if (self.artistSlidesLoadingState === LoadingState.PENDING) {
                    return;
                }

                try {
                    self.artistSlidesLoadingState = LoadingState.PENDING;

                    const data = yield slidesResource.getArtistSlides(params);

                    // @ts-expect-error
                    // TS ,     ,         ,
                    //      / ,     IDLE
                    if (self.artistSlidesLoadingState !== LoadingState.IDLE) {
                        self.artistSlidesLoadingState = LoadingState.RESOLVE;
                    }

                    if (data.slides) {
                        self.consumer = SlidesConsumer.ARTIST;
                        self.artistItems = cast(data.slides.map(slideResponseToModel));
                    }

                    self.artistSlidesLoadingState = LoadingState.RESOLVE;
                } catch (error) {
                    if (error instanceof Error) {
                        logger.error(error);
                    }
                    if (self.userSlidesLoadingState !== LoadingState.IDLE) {
                        self.artistSlidesLoadingState = LoadingState.REJECT;
                        self.artistItems = cast([]);
                    }
                }
            }),
            setActiveSlide(index: number) {
                self.activeSlide = index;
            },
            userReset() {
                self.userSlidesLoadingState = LoadingState.IDLE;
                self.userItems = cast([]);
            },
            artistReset() {
                self.artistSlidesLoadingState = LoadingState.IDLE;
                self.artistItems = cast([]);
            }
        };
    });

export type SlidesModel = Instance<typeof Slides>;
