import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceButtonTypes } from '@yandex-music-int/resource-collection';

const SlideButtonShareData = types.model('SlideButtonShareData', {
    title: types.string,
    titleColor: types.maybe(types.string),
    bgColor: types.maybe(types.string)
});

export const SlideButtonShare = types.model('SlideButtonShare', {
    type: types.literal(SlidesResourceButtonTypes.SHARE),
    data: SlideButtonShareData
});

export type SlideButtonShareModel = Instance<typeof SlideButtonShare>;
