'use client';

import { clsx } from 'clsx';
import { observer } from 'mobx-react-lite';
import { ReactNode, useCallback, useMemo } from 'react';
import { useSwiper } from 'swiper/react';

import { SlidesResourceContentCoverTypes } from '@yandex-music-int/resource-collection';
import { Icon } from '@yandex-music-int/sol/components/Icon';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import { SlideContentItemModel } from '../../../model/slide/content/shared/SlideContentItem.js';

import styles from './ChartItem.module.scss';

export enum ChartItemVariant {
    TRACKS = 'tracks',
    ARTISTS = 'artists',
    QUIZ = 'quiz'
}

interface Props {
    data: SlideContentItemModel;
    index: number;
    variant: ChartItemVariant;
    isOrderVisible?: boolean;
}

const ARTIST_ITEM_TRANSLATE = -8;
const CLIP_DELAY = 1.6;

export const ChartItem = observer(({ data, index, variant, isOrderVisible }: Props): ReactNode => {
    const swiper = useSwiper();

    const style = useMemo(() => {
        const translate = variant === ChartItemVariant.ARTISTS ? `${index * ARTIST_ITEM_TRANSLATE}px` : 0;
        return {
            '--slide-final-translate': translate,
            '--slide-clip-delay': `${index + CLIP_DELAY}s`,
            animationDelay: `${index}s`
        };
    }, [index, variant]);

    const onClickHandler = useCallback(() => {
        if (variant === ChartItemVariant.QUIZ) {
            swiper.slideNext();
        }
    }, [swiper, variant]);

    return (
        <li
            className={clsx(styles.root, styles[`root_variant_${variant}`])}
            style={style}
            onClick={onClickHandler}
            tabIndex={0}
        >
            {isOrderVisible && (
                <Heading variant="h3" size="xl" weight="black" className={styles.order}>
                    {index + 1}
                </Heading>
            )}
            <Paper
                radius={data.coverType === SlidesResourceContentCoverTypes.CIRCLE ? 'round' : 'xs'}
                className={clsx(styles.cover, styles[`cover_variant_${variant}`])}
            >
                <Image src={data.cover?.uri} withAvatarReplace fit="contain" className={styles.image} />
            </Paper>
            <div className={styles.meta}>
                {data.title && (
                    <Caption variant="span" type="text" size="l" weight="medium" lineClamp={1} className={styles.title}>
                        {data.title}
                    </Caption>
                )}
                {data.subtitle && (
                    <Caption
                        variant="span"
                        type="text"
                        size="l"
                        weight="medium"
                        lineClamp={1}
                        className={styles.subtitle}
                    >
                        {data.subtitle}
                    </Caption>
                )}
            </div>
            {variant === ChartItemVariant.QUIZ && <Icon variant="arrowRight" size="xs" className={styles.icon} />}
        </li>
    );
});
