import { ReactElement } from 'react';

import { WithClassName } from '@/shared/lib/index.js';

import { SlideModel } from '../../model/slide/Slide.js';
import { Background } from '../Background/Background.js';
import { SlideButton } from '../SlideButton/SlideButton.js';
import { SlideContent } from '../SlideContent/SlideContent.js';

import styles from './SlideCard.module.scss';

interface Props extends WithClassName {
    slide: SlideModel;
    isActive: boolean;
}

export const SlideCard = ({ slide, isActive }: Props): ReactElement => {
    return (
        <div className={styles.root}>
            <Background isActive={isActive} background={slide.background}>
                {slide.content && (
                    <div className={styles.content}>
                        <SlideContent content={slide.content} />
                    </div>
                )}
                {slide.button && <SlideButton button={slide.button} className={styles.button} />}
            </Background>
        </div>
    );
};
