import { types, Instance } from 'mobx-state-tree';

import { SlideButtonAction } from './button/SlideButtonAction.js';
import { SlideButtonShare } from './button/SlideButtonShare.js';
import { SlideButtonWave } from './button/SlideButtonWave.js';
import { SlideContentChart } from './content/variants/SlideContentChart.js';
import { SlideContentChartFavorites } from './content/variants/SlideContentChartFavorites.js';
import { SlideContentQuiz } from './content/variants/SlideContentQuiz.js';
import { SlideContentSingleEntity } from './content/variants/SlideContentSingleEntity.js';
import { SlideContentStats } from './content/variants/SlideContentStats.js';
import { SlideContentText } from './content/variants/SlideContentText.js';

export const SlideBackground = types.model('SlideBackground', {
    bgImageUrl: types.string,
    videoUrl: types.string,
    muted: types.boolean
});

export const Slide = types.model('Slide', {
    id: types.string,
    background: SlideBackground,
    button: types.maybe(types.union(SlideButtonAction, SlideButtonWave, SlideButtonShare)),
    content: types.maybe(
        types.union(
            SlideContentChart,
            SlideContentChartFavorites,
            SlideContentQuiz,
            SlideContentSingleEntity,
            SlideContentText,
            SlideContentStats
        )
    )
});

export type SlideModel = Instance<typeof Slide>;
export type SlideBackgroundModel = Instance<typeof SlideBackground>;
