'use client';

import { clsx } from 'clsx';
import { memo, ReactElement, useEffect, useRef } from 'react';

import { useToggle } from '@yandex-music-int/sol/common/hooks/useToggle.js';
import { createAvatarUrl } from '@yandex-music-int/sol/common/utils/createAvatarUrl.js';

import { WithChildren, WithClassName } from '@/shared/lib/index.js';

import { SlideBackgroundModel } from '../../model/slide/Slide.js';

import styles from './Background.module.scss';

interface Props extends WithClassName, WithChildren {
    background: SlideBackgroundModel;
    isActive: boolean;
}

const IMAGE_SIZE = 800;

const BackgroundComponent = ({ className, children, background, isActive }: Props): ReactElement => {
    const videoRef = useRef<HTMLVideoElement | null>(null);

    const {
        state: isContentVisible,
        toggleTrue: setContentVisible,
        toggleFalse: setContentInvisible
    } = useToggle(false);

    useEffect(() => {
        if (!isActive && isContentVisible) {
            setContentInvisible();
        }
    }, [isActive, isContentVisible, setContentInvisible]);

    useEffect(() => {
        if (isActive) {
            videoRef.current?.play().then(setContentVisible).catch(setContentVisible);
        } else {
            videoRef.current?.load();
        }
    }, [background.muted, isActive, setContentVisible]);

    return (
        <div className={clsx(styles.root, className)}>
            <video
                className={styles.media}
                ref={videoRef}
                src={background.videoUrl}
                poster={createAvatarUrl(background.bgImageUrl, IMAGE_SIZE, true)}
                preload="auto"
                playsInline
                muted={background.muted}
            />
            <div className={styles.fade} />
            {Boolean(children) && isContentVisible && children}
        </div>
    );
};

export const Background = memo(BackgroundComponent);
