import { ReactNode } from 'react';

import { SlidesResourceButtonTypes } from '@yandex-music-int/resource-collection';

import { WithClassName } from '@/shared/lib/index.js';

import { SlideButtonActionModel } from '../../model/slide/button/SlideButtonAction.js';
import { SlideButtonShareModel } from '../../model/slide/button/SlideButtonShare.js';
import { SlideButtonWaveModel } from '../../model/slide/button/SlideButtonWave.js';

import { ActionButton } from './ActionButton/ActionButton.js';

interface Props extends WithClassName {
    button: SlideButtonActionModel | SlideButtonShareModel | SlideButtonWaveModel;
}

export const SlideButton = ({ button, className }: Props): ReactNode => {
    switch (button.type) {
        case SlidesResourceButtonTypes.ACTION:
            if (button.data.action.weblink) {
                return <ActionButton data={button.data} className={className} />;
            }
            return null;
        case SlidesResourceButtonTypes.SHARE:
        case SlidesResourceButtonTypes.WAVE:
            return null;
    }
};
