import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentTypes } from '@yandex-music-int/resource-collection';

import { SlideContentStat } from '../shared/SlideContentStat.js';

export const SlideContentStatsData = types.model('SlideContentStatsData', {
    header: types.maybe(types.string),
    footer: types.maybe(types.string),
    stats: types.array(SlideContentStat)
});

export const SlideContentStats = types.model('SlideContentStats', {
    type: types.literal(SlidesResourceContentTypes.STATS),
    data: types.maybe(SlideContentStatsData)
});

export type SlideContentStatsModel = Instance<typeof SlideContentStats>;
export type SlideContentStatsDataModel = Instance<typeof SlideContentStatsData>;
