'use client';

import { clsx } from 'clsx';
import { ReactNode } from 'react';

import { SlidesResourceContentCoverTypes } from '@yandex-music-int/resource-collection';
import { Image } from '@yandex-music-int/sol/components/Image';
import { Paper } from '@yandex-music-int/sol/components/Paper';
import { Caption, Heading } from '@yandex-music-int/sol/components/Typography';

import { SlideContentSingleEntityDataModel } from '../../../model/slide/content/variants/SlideContentSingleEntity.js';

import styles from './SingleEntityContent.module.scss';

interface Props {
    data: SlideContentSingleEntityDataModel;
}

export const SingleEntityContent = ({ data }: Props): ReactNode => {
    return (
        <div className={styles.root}>
            {data.description && (
                <Caption variant="span" type="text" size="l" weight="medium" className={styles.description}>
                    {data.description}
                </Caption>
            )}
            <Paper
                radius={data.coverType === SlidesResourceContentCoverTypes.CIRCLE ? 'round' : 'xs'}
                className={styles.cover}
            >
                <Image src={data.cover.uri} withAvatarReplace fit="contain" className={styles.image} />
            </Paper>
            <div
                className={clsx(styles.meta, {
                    [styles.meta_withCover]: Boolean(data.smallRoundCover?.uri)
                })}
            >
                {data.smallRoundCover?.uri && (
                    <Paper radius="round" className={styles.smallRoundCover}>
                        <Image
                            src={data.smallRoundCover.uri}
                            withAvatarReplace
                            fit="contain"
                            className={styles.image}
                        />
                    </Paper>
                )}
                {data.subtitle && (
                    <Caption
                        variant="span"
                        type="text"
                        size="l"
                        weight="medium"
                        className={styles.subtitle}
                        lineClamp={1}
                    >
                        {data.subtitle}
                    </Caption>
                )}
                {data.title && (
                    <Heading variant="h3" size="xl" weight="bold" className={styles.title} lineClamp={2}>
                        {data.title}
                    </Heading>
                )}
            </div>
        </div>
    );
};
