import { SlidesResourceContentStat } from '@yandex-music-int/resource-collection';

import { SlideContentStatModel } from '../../../model/slide/content/shared/SlideContentStat.js';

export const slideContentStatResponseToModel = (response: SlidesResourceContentStat): SlideContentStatModel => {
    return {
        value: response.value,
        unit: response.unit,
        unitOverride: response.unitOverride,
        valueSuffix: response.valueSuffix
    };
};
