import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentTypes, SlidesResourceContentUnit } from '@yandex-music-int/resource-collection';

import { SlideContentItem } from '../shared/SlideContentItem.js';

export const SlideContentChartFavoritesData = types.model('SlideContentChartFavoritesData', {
    value: types.maybe(types.number),
    unit: types.maybe(types.enumeration(Object.values(SlidesResourceContentUnit))),
    unitOverride: types.maybe(types.string),
    valueSuffix: types.maybe(types.string),
    description: types.maybe(types.string),
    isOrderVisible: types.boolean,
    items: types.array(SlideContentItem)
});

export const SlideContentChartFavorites = types.model('SlideContentChartFavorites', {
    type: types.literal(SlidesResourceContentTypes.CHART_FAVORITES),
    data: types.maybe(SlideContentChartFavoritesData)
});

export type SlideContentChartFavoritesModel = Instance<typeof SlideContentChartFavorites>;
export type SlideContentChartFavoritesDataModel = Instance<typeof SlideContentChartFavoritesData>;
