import { cast } from 'mobx-state-tree';

import { SlidesResourceContentStats } from '@yandex-music-int/resource-collection';

import { SlideContentStatsModel } from '../../../model/slide/content/variants/SlideContentStats.js';
import { slideContentStatResponseToModel } from '../shared/slideContentStatResponseToModel.js';

export const slideContentStatsResponseToModel = (response: SlidesResourceContentStats): SlideContentStatsModel => {
    return {
        type: response.type,
        data: {
            header: response.data.header,
            footer: response.data.footer,
            stats: cast(response.data.stats.map(slideContentStatResponseToModel))
        }
    };
};
