import { SlidesResourceContentCover } from '@yandex-music-int/resource-collection';

import { SlideContentCoverModel } from '../../../model/slide/content/shared/SlideContentCover.js';

export const slideContentCoverResponseToModel = (response: SlidesResourceContentCover): SlideContentCoverModel => {
    return {
        uri: response?.uri,
        color: response?.color,
        videoUrl: response?.videoUrl
    };
};
