import { cast } from 'mobx-state-tree';

import { SlidesResourceContentChart } from '@yandex-music-int/resource-collection';

import { SlideContentChartModel } from '../../../model/slide/content/variants/SlideContentChart.js';
import { slideContentItemResponseToModel } from '../shared/slideContentItemResponseToModel.js';

export const slideContentChartResponseToModel = (response: SlidesResourceContentChart): SlideContentChartModel => {
    return {
        type: response.type,
        data: {
            description: response.data.description,
            isOrderVisible: response.data.isOrderVisible,
            items: cast(response.data.items.map(slideContentItemResponseToModel))
        }
    };
};
