import { types, Instance } from 'mobx-state-tree';

import { SlidesResourceContentTypes, SlidesResourceContentCoverTypes } from '@yandex-music-int/resource-collection';

import { SlideContentCover } from '../shared/SlideContentCover.js';

export const SlideContentSingleEntityData = types.model('SlideContentSingleEntityData', {
    title: types.maybe(types.string),
    subtitle: types.maybe(types.string),
    description: types.maybe(types.string),
    cover: SlideContentCover,
    coverType: types.enumeration(Object.values(SlidesResourceContentCoverTypes)),
    smallRoundCover: types.maybe(SlideContentCover)
});

export const SlideContentSingleEntity = types.model('SlideContentSingleEntity', {
    type: types.literal(SlidesResourceContentTypes.SINGLE_ENTITY),
    data: types.maybe(SlideContentSingleEntityData)
});

export type SlideContentSingleEntityModel = Instance<typeof SlideContentSingleEntity>;
export type SlideContentSingleEntityDataModel = Instance<typeof SlideContentSingleEntityData>;
